.form {
  @extend .main-card;
  margin-top: 1rem;

  &__group {
    padding: 1rem;
    display: flex;
    margin: auto;
    justify-content: space-evenly;
    select {
      background: white;
      border: 1px $dark-blue solid;
      box-shadow: 3px 2px 2px $black;
      margin-left: 10px;
    }

    select:hover {
      border: 1px solid black;
    }
  }

  &__names {
    &-container {
      display: flex;
      flex-direction: column;
      input {
        margin-top: 0.5rem;
      }
    }
  }

  button {
    margin-top: 1rem;
    width: 100%;
  }

  .validator {
    background-color: rgba(255, 255, 255, 0.3);
    min-height: 24px;
    margin-top: 1rem;
    border-radius: 10px;

    p {
      color: grey;
      font-size: 0.8rem;
      font-style: italic;
    }
  }
}
